.app-wrapper {
    box-sizing: border-box;
    display: grid;
    margin: 0 auto;
    width: 1200px;
    grid-template-areas: 
    "h h"
    "n c";
    grid-template-rows: 160px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}


a {
    text-decoration: none;
}
img {
    width: 100%;
}
.app-wrapper-content {
    grid-area: c;
    background-color: coral;
}
